
/* Footer 스타일 */
.footer {
  margin-top: 40px;
  padding: 20px;
  background-color: #333;
  color: white;
  text-align: center;
}

.footer table {
  width: 100%;
  margin: 0 auto;
}

.footer a {
  color: #fff;
  text-decoration: none;
}

.footer a:hover {
  text-decoration: underline;
}
